.login_logo {
    width: 150px;
    object-fit: contain;
    margin-bottom: 20px;
    margin-top: 20px;
    
}
.login
{
    object-fit: contain;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 160px;
}

.login_container 
{
    width: 300px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid lightgray;
    background-color:white;
}

.login_container >h1 {
    font-weight: 500;
    margin-bottom: 20px;
}

.login_container > form >h5 {
    margin-bottom: 5px;
}

.login_container > form >input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.login_container > p{
   margin-top: 15px;
   font-size: 12px;
}

.login_signInButton {
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}
.login_registerButton {
   
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
    color: #111;
}